@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.reports,
.team,
.reports {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
}



/*****MENUUU*******/
header{
    position: -webkit-sticky;
    position: sticky;
    top:0px;
    z-index: 5;
    width: 100%;
    height: 70px;
    background: #252831;
    display: flex;
    align-items: center;
    justify-content:space-between;
}
.logo-icon img{
  display: none;
}

header nav{
    background: #15171c;
  width: 100%;
  height: 70px;
  display: flex;
}
.divLink{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
  }
  .divLink:hover {
    background: #252831;
    border-bottom: 4px solid #632ce4;
    cursor: pointer;
  }

  .subMenu{
    display: flex;
    flex-direction: column;
    z-index: 120;
  }


  .linkName{
    display: flex;
    color: #e1e9fc;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    list-style: none;
    height: 70px;
    text-decoration: none;
    font-size: 18px;
  }
  .nav-div{
    width: 100%;
    display:flex;
    justify-content: flex-end;
    margin-right: 75px;
    position: relative;
  }
  #logo{
    width: auto;
    margin-left: 0;
    position: absolute;
    top: 50%;
    height: 35px;
    left: 62px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .nav-link{
    display: flex;
    flex-direction: column;
  }
  .linkName{
    font-size: 20px;
    margin-left: 10px;
  }
  #menu{
    display: none !important;
  }
  .close-slide-bar{
    font-size: 35px;
      color: white;
      font-weight: 700;
      display: flex;
      justify-content: flex-end;
      margin-right: 30px;
      margin-top: 15px;
  }
  #overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 0;
    display: none;
  }
  .close-slide-bar{
    display: none !important;
  }
  
   .bar{
     width: 30px;
     height: 3px;
     background-color: white;
     margin: 3px 0;
   }
   
   
   .itemIcon{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
   }
   .divLink svg{
       color: white;
   }
  /******** RESPONSIVE MENU ***********/
  
  @media screen and (max-width:1000px){
    header{
        position: none;
    }
    #logo{
      left: 50%;
    }
    .nav-div{
      justify-content: normal;
      margin-right: 0;
    }
    
    .openSlideBar{
        -webkit-transform: translate(0px);
                transform: translate(0px);
        transition: -webkit-transform 0.5s;
        transition: transform 0.5s;
        transition: transform 0.5s, -webkit-transform 0.5s;
       }
       .closeSlideBar{
        -webkit-transform: translate(-250px);
                transform: translate(-250px);
        transition: -webkit-transform 0.5s;
        transition: transform 0.5s;
        transition: transform 0.5s, -webkit-transform 0.5s;
       }
       .openOverlay{
         display: block !important;
       }
    
    header nav{
      width: 250px !important;
      -webkit-transform: translate(-250px);
              transform: translate(-250px);
    }
    .nav-div{
    flex-direction: column;
    background: #252831;
    height: 100vh !important;
    z-index: 33;
    }
    .close-slide-bar{
        display: flex !important;
      }
      #menu{
        display: flex !important;
        flex-direction: column;
        position: absolute;
        left: 31px;
        top: 21px;
      }
   
  }
.filterLap img{
    width: 60px;
    cursor: pointer;
    margin-left: 15px;
    padding: 10px;
    background: #557bc1;
    border-radius: 50%;
    box-shadow: 1px 2px 4px;
}
.filLap{
    position: fixed;
    top: 80px;
    z-index: 4;
}
.filterLap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 310px;
}
.filterLap div{
    color: white;
    font-size: 35px;
    font-weight: 800;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 0px;
}

.filterLap-background{
    background: #252831;
}
.filter-part{
    width: 330px;
    -webkit-transform: translate(0px);
            transform: translate(0px);
    transition: -webkit-transform ease-out 0.5s;
    transition: transform ease-out 0.5s;
    transition: transform ease-out 0.5s, -webkit-transform ease-out 0.5s;
    position: absolute;
    top: -25px;
    background: #252831;
    color: white;
    padding: 15px;
    padding-top: 100px;
    z-index: -1;
}
.closeFilterPart{
    width:330px;
    position: absolute;
    -webkit-transform: translate(-330px);
            transform: translate(-330px);
    transition: -webkit-transform ease-in 0.30s;
    transition: transform ease-in 0.30s;
    transition: transform ease-in 0.30s, -webkit-transform ease-in 0.30s;
}
.fil-brend{
    margin-bottom: 10px;
}
.fil-price{
    margin-bottom: 10px; 
}
.filter-span{
    display: flex;
    justify-content: space-between;
    width: 100%;
    background: #5d5d5d;
    align-items: center;
    padding: 5px 10px;
    font-size: 16px;
}
.filter-span div{
    padding: 10px 15px;
}
.fil-brend input{
    margin-right: 15px;
}
.filter-button{
    display: flex;
    justify-content: space-evenly;
    margin-top:25px;
}
.filter-button button{
    -webkit-appearance: none;
            appearance: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border:none;
    outline:none;
    padding: 5px 10px;
    border-radius: 10px;
    cursor: pointer;
}
.filter-none{
    display: none;
}
.filter-input{
    padding-top: 10px;
}
.save-filter{
    background-color: rgb(110, 150, 71);
    font-size: 18px;
    font-weight: 600;
    color:white;
}
.reset-filter{
    background-color: rgb(184, 43, 43);
    font-size: 18px;
    font-weight: 600;
    color:white;
}
.marginlaptop{
    margin-top:80px;
}
.filterIcon{
display: block;
}
.noneFilterIcon{
    display: none;
}
.noneCloseFilterLap{
    display: none;
}
.blockCloseFilterLap{
    display: block;
    opacity: 1;
    -webkit-animation: blockFilterLap 0.70s alternate;
            animation: blockFilterLap 0.70s alternate;
}
@-webkit-keyframes blockFilterLap{
    0%{opacity: 0;}
    50%{opacity: 0;}
    100%{opacity: 1;}
}
@keyframes blockFilterLap{
    0%{opacity: 0;}
    50%{opacity: 0;}
    100%{opacity: 1;}
}

